
.fade-enter-active,
.fade-leave-active {
  -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-timing-function: ease;
          transition-timing-function: ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0
}

.navbar nav{
  padding: 0 20px;
}

.card{
  -webkit-transition: all 0.1s;
  transition: all 0.1s;
  margin: 2em;
  width: 400px
}
.index{
  /* display: grid;
  grid-template-columns: repeat(3,1fr); */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: 50px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.index h2{
  font-size: 1.8em;
  text-align: center;
  margin:0
}
.index .ingrad{
  margin: 30px auto
}
.index .ingrad li{
  display: inline-block
}
.index .delete{
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  color: #aaa;
  font-size: 1.5em
}
.nothing{
  text-align: center;
  color: #aaa
}

.add-smoothie{
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
.add-smoothie h2{
    font-size: 2em;
    margin:  20px auto
}
.add-smoothie .field{
    margin: 20px auto;
    position: relative;
}
.add-smoothie .delete{
    cursor: pointer;
    position: absolute;
    right: 0;
    color: #aaa;
    bottom: 16px;
}

